.text {
  font-family: "Vibur", sans-serif;
  color: #fff;
  text-shadow: 0 0 7px #e91e6373, 0 0 10px #fff, 0 0 21px #fff, 0 0 42px #e91e63,
    0 0 82px #e91e63, 0 0 92px #e91e63, 0 0 44px #e91e63, 0 0 33px #e91e63;
  -webkit-animation: flicker 3s infinite alternate;
          animation: flicker 3s infinite alternate;
}
@-webkit-keyframes flicker {
  0%,
  18%,
  22%,
  25%,
  53%,
  57%,
  100% {
    text-shadow: 0 0 7px #e91e6373, 0 0 10px #fff, 0 0 21px #fff,
      0 0 42px #e91e63, 0 0 82px #e91e63, 0 0 92px #e91e63, 0 0 44px #e91e63,
      0 0 33px #e91e63;
  }

  20%,
  24%,
  55% {
    text-shadow: none;
  }
}
@keyframes flicker {
  0%,
  18%,
  22%,
  25%,
  53%,
  57%,
  100% {
    text-shadow: 0 0 7px #e91e6373, 0 0 10px #fff, 0 0 21px #fff,
      0 0 42px #e91e63, 0 0 82px #e91e63, 0 0 92px #e91e63, 0 0 44px #e91e63,
      0 0 33px #e91e63;
  }

  20%,
  24%,
  55% {
    text-shadow: none;
  }
}

